import { Avatar, Chip } from "@mui/material"
import {F, L} from "./Game"
import { chancellorStyle, fascistStyle, liberalStyle, presidentStyle } from "./theme"
import GavelIcon from '@mui/icons-material/Gavel';
import Person2Icon from '@mui/icons-material/Person4';
import PersonPinIcon from '@mui/icons-material/PersonPin';

import PanToolIcon from '@mui/icons-material/PanTool';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';

/**
 * @param {*} party 
 * @param {*} onClick 
 * @returns 
 */
export const PartyIcon = ({party, onClick}) => {

    const style = party === F ? fascistStyle : liberalStyle    
    
    return <Avatar
                style = {style}
                onClick = {onClick}
            >
                { party === F ? <PanToolIcon style={style}/> : <VolunteerActivismIcon style={style}/>}
            </Avatar>
}

export const PresidentIcon = (props) => {
    return <GavelIcon props={props} />
}
export const ChancellorIcon = (props) => {
    return <PersonPinIcon props={props} />
}
export const PlayerIcon = (props) => {
    return <Person2Icon props={props} />
}

export const PresidentChip = ({name, onClick, useTitle=true, ...props}) => {
    const label = (useTitle ? "Presidente ":"") + name
    return <Chip
            icon={<GavelIcon style={presidentStyle}/>} 
            label={label} 
            style={presidentStyle}
            onClick={onClick}
            props={props}
            />
}
export const ChancellorChip = ({name, onClick, useTitle=true, ...props}) => {
    const label = (useTitle ? "Cancelliere ":"") + name
    return <Chip
            icon={<ChancellorIcon style={chancellorStyle}/>} 
            label={label} 
            style={chancellorStyle}
            onClick={onClick}
            props={props}
            />
}
export const PlayerChip = ({name, onClick, ...props}) => {
    return <Chip
            icon={<PlayerIcon style={null}/>} 
            label={name} 
            style={null}
            onClick={onClick}
            props={props}
            />
}