import { 
    Badge, 
    Button, 
    Chip, 
    Dialog, 
    DialogContent, 
    DialogTitle, 
    Grid, 
    List, 
    Paper, 
    styled,
    TextField, 
} from "@mui/material";
import { Box, Stack } from "@mui/system";
import React, { useEffect, useState } from "react";
import GavelIcon from '@mui/icons-material/Gavel';
import ThumbDownIcon from '@mui/icons-material/ThumbDown';
import WhatshotIcon from '@mui/icons-material/Whatshot';
import AutorenewIcon from '@mui/icons-material/Autorenew';
import LayersIcon from '@mui/icons-material/Layers';
import PanToolIcon from '@mui/icons-material/PanTool';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';
import GameAppBar from "./GameAppBar";
import { SimpleDialog } from "./SimpleDialog";
import ElectionDialog from "./ElectionDialog";
import * as theme from "./theme";
import { ChancellorChip, PartyIcon, PlayerChip, PresidentChip } from "./GameComponents";

function shuffle(array) {
    let currentIndex = array.length,  randomIndex;
    while (currentIndex != 0) { // While there remain elements to shuffle.
      randomIndex = Math.floor(Math.random() * currentIndex) // Pick a remaining element.
      currentIndex--;
      [array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]]       // And swap it with the current element.
    }
    return array;
}

function listOf(n) {
    let list = []
    for (let i = 0; i < n; i++) {
        list.push(null)
    }
    return list
}
  
const StyledBadge = styled(Badge)(({ theme }) => ({
    '& .MuiBadge-badge': {  
      right: 18,
      top: 40,
      border: "none",
      backgroundColor: "transparent",
      color: "gray"
    },
  }));

export const F = "FASCISTA"
export const L = "LIBERALE"


// ---------------------------------------------
    export const ___VERSION___ = "0.0.6"
// ---------------------------------------------


const Game = () => {
    

    // dovrebbero essere 11 fasciste e 6 liberali
    const initialCards = [
        {faction: F, law: "I liberi cittadini possono essere arrestati senza mandato del tribunale."},
        {faction: F, law: "I liberi cittadini possono essere arrestati se sono gay"},
        {faction: F, law: "I liberi cittadini possono essere arrestati se sono comunisti"},
        {faction: F, law: "I liberi cittadini possono essere arrestati se sono ebrei"},
        {faction: F, law: "I liberi cittadini possono essere arrestati se sono vegani"},
       
        {faction: L, law: "Tutti i cittadini hanno diritto a un lavoro"},
        {faction: L, law: "Tutti i cittadini hanno diritto al welfare"},
        {faction: L, law: "Tutti i cittadini hanno diritto alla salute"},
    ]

    const RIOT_LIMIT = 3
    const MAX_FASCIST_LAWS = 6
    const MAX_LIBERAL_LAWS = 5

    const MIN_N_PLAYERS = 4
    const MAX_N_PLAYERS = 6

    const [cards, setCards] = useState(shuffle(initialCards))
    const [fascistLaws, setFascistLaws] = useState(0)
    const [liberalLaws, setLiberalLaws] = useState(0)
    const [riotLevel, setRiotLevel] = useState(0)
    const [drawMenuOpen, setDrawMenuOpen] = useState(false)
    const [electionDialogOpen, setElectionDialogOpen] = useState(false)
    const [playersSelectionDialogOpen, setPlayersSelectionDialogOpen] = useState(false)
    const [hand, setHand] = useState([])
    const [character, setCharacter] = useState("Presidente")
    const [winner, setWinner] = useState(undefined)
    const [alert, setAlert] = useState({open: false,  title: "", content: <></>, onClose: ()=>{}})
    const [inGame, setInGame] = useState(false)
    
    const [players, setPlayers] = useState()
    const [playerNames, setPlayerNames] = useState([])
    const [newPlayerName, setNewPlayerName] = useState("")

    const [president, setPresident] = useState(undefined)
    const [chancellor, setChancellor] = useState(undefined)

    const [lastGovernment, setLastGovernment] = useState(undefined)

    const updatePresident = () => {
        let index = undefined
        for (let i=0; i < players.length; i++) {
            if (players[i].name === president.name) {
                index = i+1
                break
            }
        }
        if (index >= players.length) {
            index = 0
        }
        setPresident(players[index])
    }

    const updateChancellor = (newCanchellor) => {
        setChancellor(newCanchellor)
    }

    // ------------------------------------ EFFECTS ------------------------------------

    useEffect(() => {
        if (!inGame) {
            setPresident(undefined)
            setChancellor(undefined)
            setLastGovernment(undefined)
            setCards(shuffle(initialCards))
            setFascistLaws(0)
            setLiberalLaws(0)
            setRiotLevel(0)
            setHand([])
            setCharacter("Presidente")
            setWinner(undefined)
            setAlert({open: false,  title: "", content: <></>, onClose: ()=>{}})
        }
    }, [inGame])

    // when the game starts, the game starts and the first player is the president
    useEffect(() => {
        if (players) {
            console.log("players", players)
            setInGame(true)
        }
    }, [players])

    // when the cards are shuffled, the alert is opened
    useEffect(() => {
        if (cards && inGame) {
            openAlert("Mazzo mischiato", )
        }
    }, [cards])

    // when the president is set, the election dialog for the chancellor is opened
    useEffect(() => {
        setChancellor(undefined)
    }, [president])

    // when the chancellor is set, the alert is opened
    useEffect(() => {
        if (president && chancellor) {
            openAlert("NUOVO GOVERNO", 
                <Stack spacing={2}>
                    <PresidentChip name={president.name} />
                    <ChancellorChip name={chancellor.name} />
                </Stack>)
        }
    }, [chancellor])

    // when the hand changes, the alert is opened
    useEffect(() => {
        if (hand) {
            if (hand.length == 2) {
                setCharacter("Cancelliere")
            }
            if (hand.length == 1) {
                setCharacter("Presidente")
                placeCard(hand[0])
                setHand([])
                setLastGovernment([president, chancellor]) // store the actual active government
            }
        }
    }, [hand])

    // when a law is placed, if the number of laws reach the max, the winner is set
    // then the alert is opened
    useEffect(() => {
        if (fascistLaws >= MAX_FASCIST_LAWS) {
            setLiberalLaws(0)
            setFascistLaws(0)
            setWinner(F)
        } else if (liberalLaws >= MAX_LIBERAL_LAWS) {
            setLiberalLaws(0)
            setFascistLaws(0)
            setWinner(L)
        } 
        if (winner) {
            openAlert("FINE PARTITA", "Il gioco è stato vinto dalla fazione " + winner)
        }
        
    }, [liberalLaws, fascistLaws])


    



    // ------------------------------------ METHODS ------------------------------------

    const handleStartGame = () => {
        // compute nFascists: it is the half of the number of players, rounded down
        let nFascists = Math.floor(playerNames.length / 2)
        
        console.log("players", playerNames.length, "\nnFascists", nFascists, "\nnLiberals", playerNames.length - nFascists - 1, "\nhitler", 1 )

        let randomNames = shuffle(playerNames)
        let hitler = randomNames[0]
        let fascists = randomNames.slice(1, nFascists)
        let liberals = randomNames.slice(nFascists)

        console.log("Hitler: ", hitler, "\nFascists: ", fascists, "\nLiberals: ", liberals)

        let newPlayers = randomNames.map((name) => { 
            return {
                name: name,
                role: fascists.includes(name) || name === hitler ? F : L,
                isHitler: (name === hitler),
                spotted: false
            }
        })
        setPlayers(newPlayers)
        setPresident(newPlayers[0]) // TODO: randomize
        setPlayersSelectionDialogOpen(false)
    }

    const closeAlert = () => {
        setAlert({...alert, open: false})
    }

    const openAlert = (title, content, onClose=()=>{}) => {
        setAlert({title: title, content: content, open: true, onClose: onClose},)
    }

    const placeCard = (card) => {
        setDrawMenuOpen(false)
        let newLaw = undefined
        if (card.faction === F) {
            setFascistLaws(fascistLaws + 1)
            
        } else if (card.faction === L) {
            setLiberalLaws(liberalLaws + 1)
         
        } else {
            console.error("invalid card", card )
        }
        newLaw = card
        openAlert(
            "Nuova riforma", 
            <>Il governo {president.name}-{chancellor.name} ha emendato una legge di stampo {newLaw.faction}: <br /><br /> {newLaw.law}</>,
            updatePresident)
    }


    const draw = () => {
        let card = cards[0]
        let newCards = cards
        newCards.shift()
        setCards(newCards)
        return card
    }

    const newProtest = () => {
        
        updatePresident()
        if (riotLevel == RIOT_LIMIT) {
            let randomCard = draw()
            openAlert(
                "È RIVOLTA!", 
                <> {"Il governo " + president.name + "-" + chancellor.name + " viene rovesciato, e casualmente una legge " + randomCard.faction + " viene attuata: "}
                    <br/>{randomCard.law}</>,
                    () => placeCard(randomCard) // mettere qui il placeCard anziché giù, permette di visualizzare questo alert anziché quello legato alla legge applicata
            )
            // placeCard(randomCard) <--- no qui
            setRiotLevel(0)
        } else {
            openAlert(
                "Governo boicottato!", 
                <> {"Il governo " + president.name + "-" + chancellor.name + " è stato sciolto!"}
                    <br/> Si torna alle urne! </>
            )
            setRiotLevel(riotLevel + 1)
        }
    }

    const handleDrawCards = () => {
        setHand([cards[0], cards[1], cards[2]])
        setDrawMenuOpen(true)
        let newCards = cards
        newCards.shift()
        newCards.shift()
        newCards.shift()
        setCards(newCards)
    }

    const handleChangeNewPlayerName = (event) => {
        setNewPlayerName(event.target.value.toUpperCase())
    }

    const handleAddNewPlayer = () => {
        if (newPlayerName && playerNames.length < MAX_N_PLAYERS) {
            setPlayerNames([...playerNames, newPlayerName])
            setNewPlayerName("")
        }
    }


    const handleLawSelected = (cardIndex) => {
        if (hand && hand.length > 1) {
            let restCard = []
            for (let i = 0; i < hand.length; i++) {
                if (i != cardIndex) {
                    restCard.push(hand[i])
                }
            }
            setHand(restCard)
        } 
    }

    const shuffleCards = () => {
        let shuffledCards = shuffle(initialCards)
        setCards(shuffledCards)
    }

    /**
     * Get the eligible players for the charge of Chancellor. They must non be the acual president or chancellor. 
     */
    const eligiblePlayers = () => {
        return players ? 
            players.filter(
            // player => (player.name != president.name) && (!chancellor || player.name != chancellor.name)
            (player) => (lastGovernment ?                                                                           // se c'è stato un governo, 
                        (player.name !== lastGovernment[0].name) && (player.name !== lastGovernment[1].name && (player.name != president.name))        // gli elegibili sono tutti tranne i due del governo precedente
                        : player.name !== president.name)                                                           // altrimenti sono tutti tranne il presidente attuale
            )
            : []
    }

    /** returns a list of all the polayers which are not contained in eligiblePlayers */ 
    const nonEligiblePlayers = () => {
        return players ?
            players.filter(
                (player) => !eligiblePlayers().includes(player)
            )
            : []
    }


    


    return <>
    {
        inGame ? 
        //! IN GAME
        <>
            <Box container="main"  sx={{width: "90%", margin: 3}}>  

                <Stack direction={"row"} spacing={2}>

                        {/* colonna carte fasciste */}
                        <Grid container direction="column-reverse" spacing={2} columns={{xs:MAX_LIBERAL_LAWS}} justifyContent="center" alignItems="center">
                            { listOf(fascistLaws)
                                .map((card, i) =>   <Grid item key={i}>
                                                        <Paper style={theme.fascistCardStyle}> 
                                                            <PanToolIcon color="fascist_icon" fontSize="large"/> 
                                                        </Paper>
                                                    </Grid> 
                            )}
                            { listOf(MAX_FASCIST_LAWS - fascistLaws)
                                .map((card, i) =>   <Grid item key={i}>
                                                        <Paper style={theme.cardStyle}> 
                                                            <PanToolIcon color="disabled" fontSize="large"/> 
                                                        </Paper>
                                                    </Grid> 
                            )}
                        </Grid>

                        {/* colonna carte liberali */}
                        <Grid container direction="column-reverse" spacing={2} columns={{xs:MAX_FASCIST_LAWS}} justifyContent="center" alignItems="center">
                            { listOf(liberalLaws)
                                .map((_, i) =>  <Grid item key={i}>
                                                    <Paper style={theme.liberalCardStyle}>
                                                        <VolunteerActivismIcon color="liberal_icon" fontSize="large"/>
                                                    </Paper>
                                                </Grid> )
                            }
                            { listOf(MAX_LIBERAL_LAWS - liberalLaws)
                                .map((_, i) =>  <Grid item key={i}>
                                                    <Paper style={theme.cardStyle}>
                                                        <VolunteerActivismIcon color="disabled" fontSize="large"/>
                                                    </Paper>
                                                </Grid>  
                            )}
                        </Grid>

                        {/* colonna livello rivolta */}
                        <Grid container direction="column-reverse" spacing={2} columns={{xs:RIOT_LIMIT}} justifyContent="center" alignItems="center">
                            {
                                listOf(riotLevel)
                                    .map((_, i) => <Grid item key={i}><Chip style={theme.riotIndicatorStyle} variant="contained" color="error"/></Grid> )
                            }
                            {
                                listOf(RIOT_LIMIT - riotLevel)
                                    .map((_, i) => <Grid item key={i}><Chip style={theme.riotIndicatorStyle} variant="outlined" color="error"/></Grid> )
                            }
                            <Grid item>
                            { 
                            <WhatshotIcon color={["noDanger", "lowDanger", "highDanger", "riot"][riotLevel]} fontSize="large"/>
                            }  
                            </Grid>
                        </Grid>
                </Stack>

                {/* CONTROL PANEL */}
                <Grid item xs={1} sx={{border:0}}>
                    <Stack spacing={3} justifyContent="space-between" direction={"row"} sx={{borderTop: 1, paddingTop: 3, marginTop: 3}}>
                        
                        {/* LAW BUTTON*/}
                        <Button 
                            variant={"contained"}
                            elevation={24} 
                            color={cards.length > 2 ? "law": "shuffle"}
                            style={theme.buttonStyle} 
                            disabled={(!president || !chancellor)}
                            onClick={cards.length > 2 ? handleDrawCards : shuffleCards}
                            startIcon={cards.length > 2 ? <GavelIcon color="lawIcon"/> : <AutorenewIcon color="lawIcon" />}
                            sx={{color: "white", fontSize: 13}}
                        > 
                            {cards.length > 2 ? "RIFORMA": "MISCHIA"}
                        </Button>
                        <Button color="noDanger">
                            <StyledBadge badgeContent={cards.length} color="primary">
                                <LayersIcon fontSize="large"/>
                            </StyledBadge>    
                        </Button>

                        {
                            president && chancellor ?
                            <Button 
                                variant={"contained"}
                                color={riotLevel < RIOT_LIMIT ? "protest" : "riot"} 
                                elevation={24} 
                                style={theme.buttonStyle} 
                                onClick={newProtest}
                                startIcon={riotLevel < RIOT_LIMIT ? <ThumbDownIcon color="protestIcon" /> : <WhatshotIcon color="riotIcon" />}
                            >
                                {riotLevel < RIOT_LIMIT ? "BOICOTTA" : "RIVOLTA!"}
                            </Button>
                            :
                            <Button 
                                variant={"contained"}
                                color={"primary"} 
                                elevation={24} 
                                style={theme.buttonStyle} 
                                onClick={() => setElectionDialogOpen(true)}
                                disabled={!!president && !!chancellor}
                            >
                                {"NUOVO GOVERNO"}
                            </Button>
                        }
                        
                        
                    </Stack>

                    <GameAppBar players={players} president={president} chancellor={chancellor} restartGameHandler={() => setInGame(false)}/>
                </Grid>

            </Box>

            {
                (president && chancellor) ? 
                // LAW BUTTON
                <Dialog 
                    open={drawMenuOpen} 
                    onClose={() => (hand.length === 1) ? placeCard("FASCIST") : {}} 
                    PaperProps={{sx: {height: 200, width: 350, borderRadius:5}}}
                    >
                    <DialogTitle> { `${character} ${hand.length > 2 ? president.name : chancellor.name}, scarti una legge.`} </DialogTitle>
                    <List key={123} sx={{ pt: 0 }}>
                        <br />
                        <Stack spacing={5} direction="row" justifyContent={"center"} alignItems={"center"}>
                        {
                            hand.map((card, i) => <PartyIcon party={card.faction} onClick={() => handleLawSelected(i)} key={i} />)
                        }
                        </Stack>
                    </List>
                </Dialog> 
                :
                <></>
            }
            {/* CHANCELLOR DIALOG */}
            <ElectionDialog 
                open = {electionDialogOpen} 
                eligiblePlayers = {eligiblePlayers()}
                nonEligiblePlayers = {nonEligiblePlayers()}
                president = {president}
                chancellor = {chancellor}
                onClose = {(newChancellor) => {setElectionDialogOpen(false); updateChancellor(newChancellor)}}
            />

            <SimpleDialog open={alert.open} title={alert.title} onClose={() => {alert.onClose(); closeAlert()}}> 
                {alert.content} 
            </SimpleDialog>
        </>

        : 

        //! NOT IN GAME
        <>
            <Box sx={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh'}}>
                <Stack>
                    <h1>SECRET HITLER</h1>
                    <Button 
                        onClick={() => setPlayersSelectionDialogOpen(true)} 
                        variant={"contained"}
                        elevation={24} 
                        style={theme.buttonStyle} >
                            Nuova partita
                    </Button>
                </Stack>
            </Box>

            <GameAppBar players={players}/>

            <Dialog 
                    open={playersSelectionDialogOpen} 
                    onClose={() => (hand.length === 1) ? placeCard("FASCIST") : {}} 
                    PaperProps={{sx: {height: 600, width: 350, borderRadius:5}}}

                    >
                    <DialogTitle> { `Seleziona da ${MIN_N_PLAYERS} a ${MAX_N_PLAYERS} giocatori`} </DialogTitle>
                    <DialogContent>

                        <Stack spacing={2}>
                            {/* textbox per inseriere un nuovo nome (attiva solo se ci sono ancora posti) */}
                            {
                                (playerNames.length < MAX_N_PLAYERS) ?
                                    <TextField 
                                        size="small" 
                                        placeholder = "Nome giocatore" 
                                        value = {newPlayerName}
                                        onChange = {handleChangeNewPlayerName}
                                        InputProps = {{
                                            endAdornment: <Button 
                                                            variant="contained" 
                                                            size="small" 
                                                            onClick={handleAddNewPlayer}
                                                            >
                                                                +
                                                        </Button>
                                        }}
                                    /> : <></>
                            }
                            
                            {/* lista dei giocatori aggiunti */}
                            {
                                playerNames.map((name) => <PlayerChip name={name} /> )                                
                            }
                        </Stack>
                    </DialogContent>
                        
                    <Button 
                        variant="contained"
                        size="LARGE"
                        onClick={handleStartGame}
                        disabled={
                            (playerNames.length < MIN_N_PLAYERS)      // there are not enough players
                            || (playerNames.length > MAX_N_PLAYERS)   // there are too many players
                            || !!newPlayerName                      // it has not been insered a new player name
                        }
                        >
                            {
                                (playerNames.length < MIN_N_PLAYERS) ? `Inserisci almeno ${MIN_N_PLAYERS} giocatori`
                                : (playerNames.length > MAX_N_PLAYERS) ? `Inserisci al massimo ${MAX_N_PLAYERS} giocatori` //! non dovrebbe mai verificarsi
                                : "Inizia"
                            }
                    </Button>
                </Dialog> 
        </>    
    }
    </>
}

export default Game;