import { Button, Chip, Dialog, DialogActions, DialogContent, DialogTitle, Grid } from '@mui/material';
import { Box, Stack } from '@mui/system';
import * as React from 'react';
import FaceIcon from '@mui/icons-material/Face';
import { lawButtonStyle } from './theme';
import { ChancellorChip, PlayerChip, PresidentChip } from './GameComponents';

/**
 * 
 * @param {boolean} open 
 * @param {array} eligiblePlayers 
 * @param {*} president 
 * @param {*} chancellor 
 * @param {any} setChancellor 
 * @returns 
 */
const ElectionDialog = ({open, eligiblePlayers, president, nonEligiblePlayers, onClose = ()=>{}}) => {

    const [selectedPlayer, setSelectedPlayer] = React.useState("")

    return <Dialog 
                open={open} 
                PaperProps={{sx: {height: 500, width: 400, borderRadius:5, padding: 3}}}
                >
                <DialogTitle variant="h5" align="center"> 
                    Nuova carica presidenziale
                </DialogTitle>
                
                <DialogContent> 
                    
                <Stack>
                    
                    {/* <Chip 
                        icon={<FaceIcon style={presidentStyle}/>} 
                        label={president.name} 
                        style={presidentStyle}
                    /> */}
                    <PresidentChip name={president.name}/>
                
                    <Box marginTop={5} marginBottom={3}>
                        { "Presidente " + president.name + ", nomini un cancelliere"} 
                    </Box>
                        <Grid container direction={"row"} columns={{xs:3}} spacing={2}>
                            {   
                            eligiblePlayers ?
                                eligiblePlayers.map((player, i) => (
                                    <Grid item key={"e"+i}>
                                        {
                                            player === selectedPlayer ?
                                            <ChancellorChip name={player.name || ""} 
                                                useTitle={false}
                                                onClick={() => setSelectedPlayer(undefined)} /> 
                                            : 
                                            <PlayerChip name={player.name || ""}
                                                onClick={() => setSelectedPlayer(player)} /> 
                                        }
                                    </Grid>
                                ))
                                : <></>
                            }
                        </Grid>
                        <Grid container direction={"row"} columns={{xs:3}} spacing={2} marginTop={2}>
                            {   
                                nonEligiblePlayers.map((player, i) => (
                                    player ? 
                                        <Grid item key={"n"+i}>
                                            <Chip 
                                                icon={<FaceIcon />} 
                                                label={player.name}                                                 
                                                disabled
                                            />
                                        </Grid> 
                                    : null
                                ))
                            }
                        </Grid>
                    </Stack>
                </DialogContent>

                
                <DialogActions>
                    <Button 
                        fullWidth 
                        // style={lawButtonStyle} 
                        variant={!selectedPlayer ? 'outlined' : "contained"}
                        onClick={() => {
                            setSelectedPlayer(undefined)
                            onClose(selectedPlayer)
                        }}
                        disabled={!selectedPlayer}>
                            Eleggi
                    </Button>
                </DialogActions>

            </Dialog>
}
export default ElectionDialog;