import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import { Typography } from '@mui/material';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export const SimpleDialog = ({open, title, onClose, children}) => {
  
  return (
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={onClose}
        PaperProps={{sx: {height: 400, width: 350, borderRadius:5}}}
      >
        <DialogTitle marginTop={10}>
            <Typography variant='h5' component={'span'} align='center'>
                {title}
            </Typography>
        </DialogTitle>
        
        <DialogContent>
          <DialogContentText>
            <Typography variant='h6' component={'span'} align='center'>
              {children}
            </Typography> 
          </DialogContentText>
        </DialogContent>

        <DialogActions >
          <Button onClick={onClose}>Continua</Button>
        </DialogActions>
      </Dialog>
  );
}