import React, {useState} from 'react';
import { Stack } from '@mui/system';
import GroupsIcon from '@mui/icons-material/Groups';
import MenuBookIcon from '@mui/icons-material/MenuBook';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import AppDrawer from './AppDrawer';
import { AppBar, Avatar, Box, Button, IconButton, Skeleton } from '@mui/material';
import { ChancellorChip, PlayerChip, PresidentChip } from './GameComponents';
import { ___VERSION___ } from './Game';

const GameAppBar = ({players, president, chancellor, restartGameHandler}) => {

    const [playersMenuOpen, setPlayersMenuOpen] = useState(false)
    const [rulesMenuOpen, setRulesMenuOpen] = useState(false)
    const [newGameMenuOpen, setNewGameMenuOpen] = useState(false)

    const toggleNewGameMenuOpen = () => {
        setNewGameMenuOpen(!newGameMenuOpen)
    }

    const togglePlayersMenu = () => {
        setPlayersMenuOpen(!playersMenuOpen)
    }

    const toggleRulesMenu = () => {
        setRulesMenuOpen(!rulesMenuOpen)
    }


    return <>
        {/* <AppBar position="fixed" color="red" sx={{ top: 'auto', bottom: 0 }}> */}
        <Box sx={{ flexGrow: 1 }}>
            <AppBar 
                position="fixed" 
                sx={{ 
                    top: 'auto', 
                    bottom: 0, 
                    height: 40, 
                    backgroundColor:"#eee", 
                    borderTopRightRadius: 20, 
                    borderTopLeftRadius: 20
                }}
                >
                
                <Stack direction={"row"} alignItems="center" justifyContent="space-evenly">

                    <IconButton onClick={toggleNewGameMenuOpen} >
                        <ExitToAppIcon />
                    </IconButton>

                    <IconButton onClick={togglePlayersMenu} >
                        <GroupsIcon />
                    </IconButton>

                    <IconButton onClick={toggleRulesMenu}>
                        <MenuBookIcon />
                    </IconButton>

                </Stack>
            </AppBar>
        </Box>

        <AppDrawer open={playersMenuOpen} onClose={togglePlayersMenu} title="" content={
            <Box>
                {   
                    players ? 
                    <Stack spacing={3}>
                        {
                            players.map((player, i) => (
                                <Stack spacing={2} key={i} direction="row" alignItems="center" justifyContent="center">
                                    { 
                                        president && player.name === president.name ? 
                                        <PresidentChip name={player.name}/>
                                        : chancellor && player.name === chancellor.name ? 
                                        <ChancellorChip name={player.name}/>
                                        :
                                        <PlayerChip name={player.name}/>
                                    }
                                </Stack>
                            ))
                        } 
                    </Stack>
                    :
                    <Stack spacing={3}>
                    {
                        [1,2,3,4].map((player, i) => (
                            <Stack spacing={2} key={i} direction="row" alignItems="center" justifyContent="space-between">
                                <Skeleton variant="circular" width={40} height={40} />  
                                <Skeleton variant="text" sx={{ fontSize: '1rem', width: 100 }} />
                                <Skeleton variant="text" sx={{ fontSize: '1rem', width: 50 }} />
                            </Stack>
                        ))
                    }
                    <Button variant='outlined' onClick={togglePlayersMenu}>Close</Button>
                    </Stack>
                }
                
            </Box>}
        />

        <AppDrawer open={rulesMenuOpen} onClose={toggleRulesMenu} title="REGOLAMENTO" content={
            <Box>
                Ai giocatori è assegnato casualmente e segretamente un ruolo tra liberali, nazisti e Hitler. Presa visione del proprio ruolo, tutti chiudono gli occhi. Se i giocatori sono 5 o 6, il nazista e Hitler aprono gli occhi e si riconoscono, dopodiché li richiudono e, al segnale convenuto, tutti li riaprono. Se i giocatori sono più di 6, tutti stringono il pugno sul tavolo, poi i nazisti aprono gli occhi per riconoscersi tra loro e per riconoscere Hitler, che avrà mantenuto gli occhi chiusi tenendo il pugno sul tavolo con il pollice alzato. Dopodiché, i nazisti richiudono gli occhi e al segnale convenuto tutti li riaprono. In questo modo i liberali non si conoscono tra loro, mentre i nazisti sanno chi sono gli altri nazisti e chi è Hitler, il quale a sua volta non sa con chi è in squadra.
                <br /><br /><small>version {___VERSION___}</small>
            </Box>}
        />


        <AppDrawer open={newGameMenuOpen} onClose={toggleNewGameMenuOpen} title="Attenzione" content={
            <Box>
                Iniziando una nuova partita perderai i progressi di quella attuale. Sei sicuro di voler continuare?
                <br /> <br /><br /> <br /><br /> <br />
                <Stack direction="row" spacing={2} justifyContent="space-between" >
                    <Button variant='outlined' onClick={toggleNewGameMenuOpen}>Annulla</Button>
                    <Button variant='contained' color="error" onClick={restartGameHandler}>RICOMINCIA</Button>
                </Stack>
                
            </Box>}
        />
    </>
    
}
export default GameAppBar;

